@use 'base/mixins' as m;
@use 'base/variables' as v;

.dashboard {
    background-color: v.$grisClaro;
    height: 100%;

    @include m.tablet {
        display: flex;
        height: 100vh;
    }

    .principal {
        flex: 1;
        overflow: scroll;

    }

    .contenido {
        padding: 2rem;

        .nombre-pagina {
            font-size: 2.8rem;
            text-align: center;

        }
    }

    .formulario {
        margin-top: 3rem;

        // Cualquier input dentro del dashboard, dentro del formulario que no sean tipo submit
        input:not([type="submit"]) {
            background-color: v.$blanco;
        }

        input[type="submit"] {
            @include m.boton(v.$grisAzul, v.$blanco);
            font-size: 1.4rem;
            padding: 1rem;
            border-radius: 1rem;
        }
    }

    .enlace {
        color: v.$naranjaOscuro;


        &:hover {
            color: darken(v.$naranjaOscuro, 20%);
        }
    }
}